import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

let store = new Vuex.Store({
  state:{
    carPanelData:[],//
    buyNowData:[],//
    maxOff:false, //
    carShow:false,//
    ball:{//
      show:false,
      el:null,
      img:''
    },
    info:{
    mobile:'',
    id:'',
    },
    orderList:[]//
  },
  mutations:{
   addCarPanelData(state,payload){ //
      
      if(!state.ball.show){ //
        
        let carPanelData = state.carPanelData //
        
        let exist = false // + 1  
        
        carPanelData.forEach((item)=>{
          if(item.goods_id === payload.data.goods_id){
            exist = true
            item.count+= payload.count
            if(item.count > item.limit_num){//
              item.count-= payload.count
              state.maxOff = true
              return
            }
            state.carShow = true //
            
            // 
            state.ball.img = payload.data.goods_big_logo
            state.ball.show = true
            // console.log(event)
            state.ball.el = event.path[0]
            
          }
        })
        
        if(!exist){
          Vue.set(payload.data,'count',payload.count)
          Vue.set(payload.data,'checked',true)
          carPanelData.push(payload.data)
          state.carShow = true //
          
          // 
          state.ball.img = payload.data.goods_big_logo
          state.ball.show = true
          // console.log(event)
          state.ball.el = event.path[0]
        }
        
      }
      
    },
    delCarPanelData(state,data){//
      state.carPanelData = state.carPanelData.filter((item)=>{
        return item.goods_id !== data.goods_id
      })
    },
    closePrompt(state){//
      state.maxOff = false
    },
    openPrompt(state){
      state.maxOff = true
    },
    showCar(state){ //
      state.carShow = true
    },
    hideCar(state){ //
      state.carShow = false
    },
    plusCount(state,data){ //()
      state.carPanelData.forEach((item)=>{
        if(Number(item.goods_id) === Number(data.goods_id)){
          item.count++
          if(item.count > item.limit_num){
            item.count--
          }
          return
        }
      })
    },
    supCount(state,data){ //
      state.carPanelData.forEach((item)=>{
        if(Number(item.goods_id) === Number(data.goods_id)){
          item.count--
          if(item.count < 1){
            item.count++
          }
          return
        }
      })
    },
    toogleGoods(state,data){ //
      state.carPanelData.forEach((item)=>{
        if(Number(item.goods_id) === Number(data.goods_id)){
          item.checked = !item.checked
          return
        }
      })
    },
    receiveAMsg(state, payload) {
      // Astate
      state.AMsg = payload.AMsg
    },
    receiveBMsg(state, payload) {
      // Bstate
      state.BMsg = payload.BMsg
    },
    toogleAllcheck(state,isAllChecked){ //&
      state.carPanelData.forEach((item)=>{
        item.checked = !isAllChecked
      })
    },
    delAllcheckGoods(state){//
      let index = state.carPanelData.length
      while(index --) {
        if(state.carPanelData[index].checked){
          state.carPanelData.splice(index,1)
        }
      }
    },
    saveReceive(state,receive){//
      if(receive.default){
        state.receiveInfo.forEach((item)=>{
          item.default = false
        })
      }
      state.receiveInfo.push(receive)
    },
    reviseReceive(state,data){//
      state.receiveInfo.forEach((item,index)=>{
        if(data.receive.default){
          item.default = false
        }
        if(index == data.index){
          Object.assign(item,data.receive)
        }
      })
    },
    deleteReceive(state,index){//
      state.receiveInfo = state.receiveInfo.filter((item,i)=>{
        return i !== index
      })
    },
    submitOrder(state,orderData){//
      state.orderList.unshift(orderData) //
      
      if(orderData.goodsData[0].isBuyNow){// 
        state.buyNowData = []
        return
      }
      
      let index = state.carPanelData.length //
      while(index --) {
        if(state.carPanelData[index].checked) {
          state.carPanelData.splice(index,1)
        }
      }
    },
    orderGetPay(state,id){ //
      state.orderList.forEach((order)=>{
        if(Number(order.orderId) === Number(id)){
          order.isPay = true
          return
        }
      })
    },
    buyNow(state,payload){//
      let data = payload.data
      Vue.set(data,'count',payload.count)
      Vue.set(data,'isBuyNow',true)
      state.buyNowData.push(data)
    }
  },
  getters:{
    calTotalCount(state){ //
      let count = 0
      state.carPanelData.forEach( (item) => {
        count +=  item.count
      })
      return count
    },
    calTotalPrice(state){//
      let price = 0
      state.carPanelData.forEach( (item) => {
        price +=  item.goods_price * item.count
      })
      return price
    },
    isAllChecked(state){ //
      let isAllChecked = !state.carPanelData.some((item)=>{
        return !item.checked
      })
      return isAllChecked
    },
    goodsCheckedCount(state){ //
      let count = 0
      state.carPanelData.forEach((item)=>{ 
        if(item.checked){
          count += item.count
        }
      })
      return count
    },
    goodsCheckedPrice(state){ //
      let price = 0
      state.carPanelData.forEach((item)=>{ 
        if(item.checked){
          price += item.goods_price * item.count
        }
      })
      return price
    },
    goodsCheckedData(state){ //
      let checkedData = []
      state.carPanelData.forEach((item)=>{ 
        if(item.checked){
          checkedData.push(item)
        }
      })
      return checkedData
    },
    goodsNowBuyPrice(state){ //
      let price = 0
      price = state.buyNowData[0].goods_price * state.buyNowData[0].count
      return price
    }
  }
})
export default store


