<template>
  <li class="nav-cart" @mouseenter="showCar($event)" @mouseleave="hideCar($event)">
    <a href="javascript:;" class="ball-rect" @click="shoplist"></a>
    <!--class-->
    <span class="cart-empty-num" :class="{'cart-num':calTotalCount}">
      <i>{{calTotalCount}}</i>
    </span>
    <div class="nav-cart-wrapper" v-show="carShow">
      <div class="nav-cart-list">
        <div class="empty" v-if="!calTotalPrice">
          <h3></h3>
          <p>!</p>
                      </div>
                      <div class="full" v-else>
                        <div class="nav-cart-items">
                          <ul>
                            <li class="clear" v-for="item in carPanelData">
                              <div class="cart-item js-cart-item cart-item-sell">
                                <div class="cart-item-inner">
                                  <div class="item-thumb">
                                    <img :src="item.goods_big_logo">
                                  </div>
                                  <div class="item-desc">
                                    <div class="cart-cell">
                                      <h4>
                                      <a href="#/item/100027401">{{item.title}}</a>
                        </h4>
                        <h6>
                          <span class="price-icon"></span><span class="price-num">{{item.goods_price}}</span><span class="item-num">x {{item.count}}</span>
                        </h6>
                      </div>
                    </div>
                    <div class="del-btn" @click = "delCarPanelData(item)"></div>
                  </div>
                </div>
              </li>
            </ul>
          </div>
          <div class="nav-cart-total">
            <p> <strong class="ng-binding">{{calTotalCount}}</strong> </p>
            <h5><span class="price-icon"></span><span class="price-num ng-binding" ng-bind="cartMenu.totalPrice">{{calTotalPrice}}</span></h5>
            <h6>
              <router-link class="nav-cart-btn" :to="{ name: 'shopList'}"></router-link>
            </h6>
          </div>
        </div>
      </div>
    </div>
    <!--  -->
    <transition
      name="ball"
      v-on:before-enter="beforeEnter"
      v-on:enter="enter"
      v-on:after-enter="afterEnter"
      v-bind:css="true"
    >
      <div class="addcart-mask" v-show="ball.show" @mouseleave="ballLeave">
        <img class="mask-item"/>
      </div>
    </transition>
  </li>
</template>
<script>
export default {
  name: "carPanel",
  data: () => ({
    mousetoBall:false,
    carPanelData:[] // 
  }),
  computed:{
    calTotalCount(){//
      return this.$store.getters.calTotalCount
    },
    calTotalPrice(){//
      return this.$store.getters.calTotalPrice
    },
    carShow(){//
      return this.$store.state.carShow
    },
    ball(){
      return this.$store.state.ball
    }
  },
  mounted(){
    this.getcart()
  },
  methods: {
    shoplist(){
     this.$router.push('/shoplist')
    },
   async delCarPanelData(data) {//
      this.$store.commit('delCarPanelData',data)
     const userid = window.sessionStorage.getItem('userid')
      let form ={
        cart_info:this.carPanelData,
        id:userid
      }
      this.$http.post('http://api2.toyss.me/cart.php',form)
    },
   async getcart(){
      const userid = window.sessionStorage.getItem('userid')
      const {data:res}=await this.$http.post('http://api2.toyss.me/getcart.php',{id:userid})
      this.carPanelData=res
  },
    showCar(ev){//
      this.$store.commit('showCar')
    },
    hideCar(ev){//
      if(!this.mousetoBall){// 
        this.$store.commit('hideCar')
      }
      this.mousetoBall = false
    },
    ballLeave(){//
      this.mousetoBall = true
    },
    // 
    beforeEnter(el){//
      let rect = this.ball.el.getBoundingClientRect() //
      let rectEl = document.getElementsByClassName('ball-rect')[0].getBoundingClientRect() //
      let ball = document.getElementsByClassName('mask-item')[0]//
      let x = rectEl.left - rect.left  - rect.width/2//   
      let y = rect.top - rectEl.top   //   


      el.style.transform = 'translate3d(0,'+y+'px,0)'
      ball.style.transform = 'translate3d(-'+x+'px,0,0)'
      ball.src = this.ball.img

    },
    enter(el){
      let a = el.offsetHeight //
      let ball = document.getElementsByClassName('mask-item')[0]//
      el.style.transform = 'translate3d(0,0,0)'
      ball.style.transform = 'translate3d(0,0,0)'
    },
    afterEnter(){
      this.ball.show = false
    }
  },
}
</script>
<style>
/*  */
.addcart-mask{
  position: absolute;
  left:0;
  top: 0;
  z-index: 9999;
  width: 32px;
  height: 32px;
  border-radius: 50%;
}
.mask-item{
  width: 100%;
  height: 100%;
  border-radius: 50%;
}
.ball-enter-active{
  transition: 500ms cubic-bezier(.04,.58,.82,1.26);
}
.ball-enter-active .mask-item{
  transition: 500ms cubic-bezier(0,0,1,1);
}
</style>



