<template>
  <div class="login_container">
    <div class="login_box">
      <!--  -->
      <div class="avatar_box">
        <img src="../assets/logo.png" alt="avatar" />
      </div>
      <!--  -->
      <div>
        <el-form
          ref="loginFormRef"
          :model="loginForm"
          :rules="loginFormRules"
          label-width="60px"
          class="login_form"
        >
          <el-form-item label="" prop="username">
            <el-input v-model="loginForm.username" prefix-icon="iconfont icon-user"></el-input>
          </el-form-item>
          <el-form-item label="" prop="password">
            <el-input
              v-model="loginForm.password"
              type="password"
              prefix-icon="iconfont icon-3702mima"
            ></el-input>
          </el-form-item>
          <el-form-item label="">
            <el-input v-model="loginForm.mobile" prefix-icon="iconfont icon-user"></el-input>
          </el-form-item>
          <el-form-item class="btns">
            <el-button type="primary" @click="login"></el-button>
            <el-button type="info" @click="tologin"></el-button>
          </el-form-item>
        </el-form>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data () {
    return {
      loginForm: {
        username: '',
        password: '',
        mobile:'',
        rid:32,
      },
      // 
      loginFormRules: {
        username: [
          { required: true, message: '', trigger: 'blur' },
          { min: 2, max: 11, message: '11', trigger: 'blur' }
        ],
        password: [
          { required: true, message: '', trigger: 'blur' },
          { min: 6, max: 18, message: ' 6  18 ', trigger: 'blur' }
        ]
      }
    }
  },
  methods: {
    // 
    tologin () {
      // console.log(this)
      // resetFieldselement-ui
      this.$router.push('/login')
    },
    login () {
      window.sessionStorage.setItem('token', 'Bearer eyJhbGciOiJIUzI1NiIsInR5cCI6IkpXVCJ9.eyJ1aWQiOjUwMCwicmlkIjowLCJpYXQiOjE1ODg0MjYxMjMsImV4cCI6MTU5NzA2NjEyM30.97r22iZTEoyCnUuGBEfjmcGNRHAfJPerFLVXscGqumo')
      // 
      // validbool
      this.$refs.loginFormRef.validate(async valid => {
        // console.log(valid)
        if (!valid) return false
        // this.$http.post('login', this.loginForm): promise
        // promiseawait async
        const { data: res } = await this.$http.post('http://api.toyss.me/api/private/v1/users', this.loginForm)
        if (res.meta.status !== 201) return this.$message.error(res.meta.msg)
        this.$message.success('')
        // 1token, sessionStorage; localStorage
        //   1.1 API
        //   1.2 token tokensessionStorage
        // 2, /home
        window.sessionStorage.clear()
        this.$router.push('/login')

      })
    }
  }
}
</script>

<style lang="less" scoped>
/* // lang="less" less
// scoped vue */
.login_container {
  background-color: #2b4b6b;
  height: 100%;
}
.login_box {
  width: 450px;
  height: 360px;
  background-color: #fff;
  border-radius: 3px;
  position: absolute;
  left: 50%;
  top: 50%;
  -webkit-transform: translate(-50%, -50%);
  background-color: #fff;

  .avatar_box {
    width: 130px;
    height: 130px;
    border: 1px solid #eee;
    border-radius: 50%;
    padding: 10px;
    box-shadow: 0 0 10px #ddd;
    position: absolute;
    left: 50%;
    transform: translate(-50%, -50%);
    background-color: #fff;
    img {
      width: 100%;
      height: 100%;
      border-radius: 50%;
      background-color: #eee;
    }
  }
}
.login_form {
  position: absolute;
  bottom: 10px;
  width: 100%;
  padding: 0 20px;
  box-sizing: border-box;
}
.btns {
  display: flex;
  justify-content: center;
}
.info {
  font-size: 13px;
  margin: 10px 15px;
}
</style>



